* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

input#datetime-picker {
  text-align: center;
  background: #d3dad3;
  border: none;
  width: 200px;
  height: 32px;
  margin-right: 12px;
  font-weight: 600;
  box-shadow: 0 6px 8px #a8a59f, 0 -2px 1px #fff;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  font-size: 14px;
  display: inline-block;
  box-shadow: 0 3px 4px #9c9995;
}

.timer {
  margin-top: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  margin-right: 30px;
  display: flex;
}

.value {
  text-align: center;
  letter-spacing: 4px;
  color: #523111;
  font-size: 60px;
  font-weight: 600;
}

.label {
  text-align: center;
  text-transform: uppercase;
  color: #0400ff;
  font-size: 20px;
  font-weight: 400;
}

button[type="submit"] {
  color: #361f1f;
  text-shadow: 1px 1.6px 1px #fff;
  background-color: #f7eff4;
  border-radius: 50px;
  width: 80px;
  height: 40px;
  margin-left: 10px;
}

